@import url(https://fonts.googleapis.com/css2?family=Mulish&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Mulish', sans-serif;

}
.navbar {
  background-color: #fff !important;
  }
.navbar-brand {
  font-size: 2rem !important;
  color: #565875 !important;
  
}
.navbar a {
  font-size: 1.3rem;
  text-transform: capitalize;
}

.menu_active {
  font-size: bold;
  border-bottom: 1px solid #565387 ;
}
.navbar a:hover {
  color: #3498db !important;
}

@media (max-width:991px)
{
  .navbar-brand{
    font-size: 2rem;
  }

.menu_active {
font-weight: bold;
border-bottom: none;
}
}

/* logo */
.logo {
  border-radius: 50%;
  height: 60px;
  width: 70px;
}



/* landing page of home */
#header {
  width:100%;
  height: 77vh;
  margin-top: 70px;
  
}
#header h1 {
  margin: 0 0 10px 0;
  font-size: 48px;
  font-weight: 700;
  color: black;
}
#header .brand-name{
  color: #3498db;
}
#header h2 {
  color: #484848;
  font-size: 24px;
}
#header .btn-outline-primary {
  font-weight:500;
  font-size: 16px;
  letter-spacing: 1px;
  display: inline-block;
  padding: 10px 30px;
  border-radius: 50px;
  transition: 0.5s;
  color: #3498db;
  border: 2px solid #3898db;
  text-decoration: none;
}
#header .btn-outline-primary:hover{
  background: #3498dbdb;
  color: #fff;
}
.header-img {
  text-align: right;
}
#header .animated {
  animation: up-down 2s ease-in-out infinite alternate-reverse both;
}
@-webkit-keyframes up-down {
  0%{
    transition: translateY(10px);
  }
  100%{
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
}
@keyframes up-down {
  0%{
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
  }
  100%{
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
}
#services .animated {
  animation: up-down 2s ease-in-out infinite alternate-reverse both;
}
@-webkit-keyframes up-down {
  0%{
    transition: translateY(10px);
  }
  100%{
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
}
@keyframes up-down {
  0%{
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
  }
  100%{
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
}


.card-img-top {
  height:120px;
  width: 180px;
}

/* responsive mobile */


@media (max-width:991px){
  #header{
    height: 80vh;
    text-align: center;
  }
  #header .header-img {
    text-align: center;
  }
  #header .header-img img{
    width: 60%;
  }
}
@media (max-width:768px) {
  #header {
    margin: 20px;
  }
  #header h1 {
    font-size: 28px;
  }
  #header h2{
    font-size: 18px;
    margin-bottom: 30px;
  }
  #header .header-img img {
    width:70;
  }
}
